
import { defineComponent, computed } from 'vue';
import IconSun from '@atomic/atoms/svgs/IconSun.vue';
import IconMoon from '@atomic/atoms/svgs/IconMoon.vue';

export default defineComponent({
  name: 'TimeOrganism',
  components: { IconSun, IconMoon },
  props: {
    timeZoneCode: {
      type: String,
      default: '',
    },
    time: {
      type: String,
      default: '',
    },
    countryCode: {
      type: String,
      default: '',
    },
    city: {
      type: String,
      default: '',
    },
    isNight: {
      type: Boolean,
      default: false,
    },
    isAfternoon: {
      type: Boolean,
      default: false,
    },
  },
  setup(props) {
    const greetingText = computed(() => {
      let greeting = 'Good morning';
      if (props.isAfternoon) greeting = 'Good afternoon';
      if (props.isNight) greeting = 'Good evening';
      return greeting;
    });
    return { greetingText };
  },
});
